import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"

class Layout extends React.Component {
  render() {
    const { location, title, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    if (location.pathname === rootPath) {
      header = (
        <h5
        >
          <Link
            style={{
              boxShadow: `none`,
              fontFamily: `Lato`,
              fontWeight: 500,
              textDecoration: `none`,
              color: `#4b5b60`,
	      fontSize: 32,
	      paddingBottom: 0
            }}
            to={`/`}
          >
            {title}
          </Link>
	<hr  />
        </h5>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Lato`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              fontFamily: `Lato`,
              boxShadow: `none`,
              textDecoration: `none`,
              color: `#4b5b60`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <div
        style={{fontFamily: `Lato`,
	backgroundColor: '#fbfbfe',
        boxShadow: `none`,
        maxWidth: rhythm(270), maxHeight: rhythm(700), textDecoration: 'none'}}	      
      >
        <header style={{ textDecoration: 'none'}}>


	<link rel="stylesheet" type="text/css"  href="https://laurahanu.github.io/css/bootstrap.css"/>
	<link rel="stylesheet" type="text/css" href="https://laurahanu.github.io/fonts/font-awesome/css/font-awesome.css"/>

	<script async defer src="https://buttons.github.io/buttons.js"></script>

	<body id="page-top" data-spy="scroll" data-target=".navbar-fixed-top">
	<div id="nav" >
	  <nav class="navbar navbar-custom">
	    <div class="container">
	      <div class="navbar-header">
		<button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-main-collapse"> <i class="fa fa-bars"></i> </button>
		<a class="navbar-brand page-scroll" href="#page-top">Laura Hanu</a> </div>
		  <div class="collapse navbar-collapse navbar-right navbar-main-collapse">
		    <ul class="nav navbar-nav">
		      <li class="hidden"> <a href="#page-top"></a> </li>
		      <li> <a class="page-scroll" href="https://laurahanu.github.io/#about">About</a> </li>
		      <li> <a class="page-scroll" href="https://laurahanu.github.io/#portfolio">Projects</a> </li>
		      <li> <a class="page-scroll" href="https://laurahanu.github.io/#resume">Timeline</a> </li>
		      <li> <a class="page-scroll" href="https://laurahanu.github.io/blog/">Blog</a> </li>
		      <li> <a class="page-scroll" href="https://laurahanu.github.io/#contact">Contact</a> </li>
		    </ul>
		</div>
	    </div>
	  </nav>
	</div> 
	</body>


	{header}
	</header>
        <main style={{marginLeft: `auto`, marginRight: `auto`, fontWeight: 350, fontSize: 15, maxWidth: rhythm(34), paddingBottom: 20,  marginTop: 20}} >
		{children}</main>
        <footer style={{ boxShadow: `none`,color: `#4b5b60`, fontWeight: 200, fontSize: 11, marginLeft: `auto`, marginRight: `auto`, maxWidth: rhythm(34) }}>
          © {new Date().getFullYear()}, Built with
          {` `}
          <a  style={{ boxShadow: `none`,color: `#4b5b60`,fontWeight: 300 }} href="https://www.gatsbyjs.org">Gatsby</a>
        </footer>

      </div>
    )
  }
}

export default Layout
